import React from 'react';
let MoneyButton = require('@moneybutton/react-money-button').default

function convertToHex(str) {
    var hex = '';
    for (var i = 0; i < str.length; i++) {
      hex += '' + str.charCodeAt(i).toString(16);
    }
    return hex;
  }


export default class MobileVoteAlert extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            votePrice: this.props.votePrice,
            priceInput:this.props.votePrice,
            validPrice:true
        }
    }

    changePrice = (price) =>{
        document.getElementById('name-2').value=price;
        this.setState({
            votePrice:price
        })
    }

    priceInputChangeHandler = event =>{
        var priceInputValue = event.target.value;
        var NaN = isNaN(priceInputValue);
        var validPrice=false;
        var price;
    
        if(!NaN){
          if(priceInputValue=="0."||priceInputValue=="0.0"){
            price=0;
          } else{
            var price = JSON.parse(event.target.value)
          }
          
          if(price.countDecimals()<3 & price>=0.01){
            validPrice=true;
          }
        } 
        this.setState({
          priceInput: event.target.value,
          validPrice
          
        })
      }

    render() {
        var postTxid = this.props.votingOn.tx.h;
        var address = this.props.votingOn.in[0].e.a
        var validPrice = this.state.validPrice;
        var buttonClass;
        validPrice?buttonClass='header-button':buttonClass='header-button button-greyed'

        return (
            <div class="new-reply-container m-vote-container" style={{ bottom: 75 }} >
                <div class="m-vote-price-form-block w-form">
                    <form id="email-form-2" name="email-form-2" data-name="Email Form 2" class="m-vote-price-form">
                        <div  >
                            <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                                <div class="post-price-container" style={{ paddingTop: 10 }}  >
                                    <div class="header-button" onClick={()=>this.changePrice('1')} >
                                        <div class="header-button-text dropdown-text">$1</div>
                                    </div>
                                    <div class="header-button" onClick={()=>this.changePrice('0.5')} >
                                        <div class="header-button-text dropdown-text">$0.50</div>
                                    </div>
                                    <div class="header-button" onClick={()=>this.changePrice('0.05')} >
                                        <div class="header-button-text dropdown-text">$0.05</div>
                                    </div>
                                    <div class="header-button" onClick={()=>this.changePrice('0.01')} >
                                        <div class="header-button-text dropdown-text">$0.01</div>
                                    </div>

                                </div>
                                <div class="login-cancel" onClick={this.props.xHandler} >X</div>
                            </div>

                            <div class="post-price-container" style={{ paddingTop: 10 }}  >
                                <input  onChange={this.priceInputChangeHandler} type="text" pattern="\d*" inputMode='decimal' class="text-field price-field w-input" maxlength="256" defaultValue={this.state.votePrice}
                                    name="name-2" data-name="Name 2" placeholder="Vote Price (USD)" id="name-2" />
                                <div class={buttonClass} onClick={validPrice?()=>this.changePrice(this.state.priceInput):()=>null} >
                                    <div class="header-button-text dropdown-text">set price.</div>
                                </div>

                            </div>

                            <div style={{ paddingTop: 10 }}>
                                <MoneyButton
                                    label='vote'
                                    onPayment={this.props.xHandler}

                                    outputs={[
                                        {
                                            amount: 0,
                                            currency: 'BSV',
                                            type: 'SCRIPT',
                                            script: 'OP_RETURN ' + convertToHex('13beK8bifnhuC8pawWvMrPCaWoTQi5LnZf') + ' ' + convertToHex('post/vote') + ' ' + convertToHex('vote') + ' ' + convertToHex(postTxid) + ' ' + convertToHex(this.props.userId)

                                        }, {
                                            to: address,
                                            amount: this.state.votePrice,
                                            currency: 'USD',


                                        }

                                    ]}


                                />
                            </div>



                        </div>
                    </form>

                </div>
            </div>
        );
    }
}